:root {
    --background: #f4f0e8; /* Warm cream */
    --foreground: #3a3d3a; /* Warm charcoal */
    --card-background: #fefcf9; /* Warm white */
    --border-color: #e5e1db; /* Soft warm gray */
}

.dark {
    --background: #3a3d3a; /* Warm charcoal */
    --foreground: #f4f0e8; /* Warm cream */
    --card-background: #5a6b57; /* Dark sage */
    --border-color: #a8a5a0; /* Warm medium gray */
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html,
body {
    max-width: 100vw;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

body {
    color: var(--foreground);
    background: var(--background);
    font-family: 'Inter', sans-serif;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
    line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Playfair Display', serif;
    font-weight: 500;
    letter-spacing: -0.02em;
    line-height: 1.2;
}

/* Custom scrollbar with cozy colors */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--background);
}

::-webkit-scrollbar-thumb {
    background: #8fb085;
    border-radius: 12px;
}

::-webkit-scrollbar-thumb:hover {
    background: #7a9c70;
}

/* Cozy design system utility classes */

/* Cozy animations */

/* Enhanced focus styles for accessibility with cozy colors */
.focus-visible:focus {
    outline: 2px solid #8fb085;
    outline-offset: 2px;
    border-radius: 8px;
}

/* Cozy loading spinner */
.spinner-cozy {
    width: 40px;
    height: 40px;
    border: 4px solid #e5e1db;
    border-top: 4px solid #8fb085;
    border-radius: 50%;
    animation: spin 1.5s ease-in-out infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Decorative elements for cozy feel */
.decorative-dots::before {
    content: "• • •";
    color: #d4a574;
    font-size: 1.5rem;
    letter-spacing: 0.5rem;
    opacity: 0.6;
}

.decorative-leaf::after {
    content: "🌿";
    margin-left: 0.5rem;
    opacity: 0.7;
}

/* Soft shadows for depth */
.shadow-soft {
    box-shadow: 0 4px 20px rgba(143, 176, 133, 0.1);
}

.shadow-warm-soft {
    box-shadow: 0 4px 20px rgba(212, 165, 116, 0.15);
}

